import React from 'react'

const ChildMemoryPage = () => {
  return (
    <div>
        <div className="container light">
            <h1 className='bold' style={{textAlign: 'center'}}>Вместо детства - война</h1>
            <p>Все дальше уходит от нас в историю Великая Отечественная война. Война, виновная в гибели многих и многих людей, в разрухе и голоде. А еще укравшая у мальчиков и девочек детство — настоящее, солнечное, с книжками и школьными тетрадями, смехом, играми и праздниками. Детская память цепко хранит воспоминания о тех страшных годах, и сейчас эти ребята, которые давно уже стали бабушками и дедушками и которым уже за 80, могут многое рассказать о войне. Рассказ-воспоминание глусчанина Василия Ивановича ГНЕДЬКО. Начало Родился я в 1932 году на Дальнем Востоке, в Амурской области.</p>
            <p>Природа там очень красивая. Летом жара, дыни, арбузы растут, а зимой морозы под минус 50. Тайга рядом. Отец боялся, что арестуют, поэтому мы переехали в Глуск, на родину матери, родители купили здесь маленький домик по улице Советской. Было это в 39-м. Я еще успел в школу пойти (в 8 лет) и 1-й класс закончить, а потом началась война.</p>
            <p>Мост По дороге в сторону Бобруйска было два моста деревянных, возле одного будка была. Перед войной там поставили милиционера, чтоб охранял. Но его убили, и он потом дня два лежал возле этого моста, а мост сожгли. Потом парни молодые, которые в полицаи пошли, охраняли этот мост. А танки немецкие просто перешли через реку, им сожженный мост не помешал. Война Когда объявили, что война, мне было девять, пошел десятый год.</p>
            <p>Отца с первых дней войны мобилизовали. Отправляли на фронт поездом, и кто не успел на этот поезд, остались дома, кто-то потом в партизаны ушел, кто-то все-таки на фронт попал. Отец прошёл всю войну, дошёл до Берлина. С мамой нас осталось четверо: я, мой брат и сестры, старшая и младшая. До войны все знали, что у нас договор о ненападении с Германией, советская пропаганда говорила, что немцы наши друзья. И когда пришли первые немецкие танки в Глуск, наша соседка еврейка Роза вышла встречать их с цветами.</p>
            <p>Но потом все быстро поняли, какие «друзья» пришли на нашу землю. Самолет Каникулы ведь были, и мама отправляла меня в Карницу к бабушке с дедушкой помогать пасти овец. Нужно было пешком идти 6 километров до деревни, и ничего, ходил! Дед сплел маленькие лапти, и мне интересно было, я никогда в жизни не носил лаптей. А бабушка портяночки дала. Я бегу да, бывало, как зацеплюсь, потому что непривыкший был к лаптям.</p>
            <p>Бабушка пекла гречневые блины, поливала жиром и давала с собой. Главным пастухом был дед Игнат, а ему помогали подпасвичи с каждой хаты по очереди. Между Карницей и Ольницей было урочище Угода, теперь уже заросло там все. В этом урочище стоял отряд казаков-кавалеристов, о которых недавно в газете писали, вот они рядом с нашими овечками на большой поляне пасли своих лошадей. Вдруг появился самолет с крестами, очень низко летел над лесом и строчил из пулемета. Я деда спрашиваю, а почему этот самолет с крестами.</p>
            <p>Я же видел самолет и знаю, что на нем должны быть звезды. Дед и говорит мне, что это немцы, немецкий самолет. Кони так страшно храпели, убегали, но многие попадали. На моих глазах, получается, их расстреляли. А вот казаков убитых не видел, хотя, думаю, там их много полегло. Школа В сентябре 1941 года открылась даже школа, мы пошли во второй класс.</p>
            <p>Мария Филипповна Касько до войны была учительницей начальных классов, она нас снова учила. Только теперь в школе были немецкие порядки. Мария Филипповна уговаривала нас учиться, пока есть возможность, и не спорить с немцами, боялась за нас, детей. Мы должны были приветствовать учителя или того, кто входит в класс, словами «Хайль Гитлер! ». Вместе с учителем заходил немецкий офицер, проверял, как нас научили, говорил «Гут, гут!» и уходил.</p>
            <p>Часто приходил священник по фамилии Цуканов, читал молитвы и учил нас молиться. Потом мы узнали, что он был советским подпольщиком. Но школу быстро закрыли, и мы до освобождения больше не учились. Евреи Мы жили в районе еврейской слободы. Некоторые евреи уехали, когда началась война.</p>
            <p>Немцы нашли человека, который рассказал, где живут еврейские семьи, а где русские. И на каждом доме написали буквы «Р» (русский) или «Ю» (юда), да чем-то таким стойким, что и после войны эти буквы долго не стирались. Никто не знал, для чего эти буквы написали. Только потом поняли, когда в декабре 41-го евреев согнали на Мыслочанскую гору и расстреляли. Я дружил с еврейской девочкой Мостовой Фалей, на год меня старше, даже влюблен был в нее. Ой, и красавица была!</p>
            <p>Когда всех евреев сгоняли, она была у подружки по соседству. А родителей ее забрали и расстреляли вместе со всеми. Так сосед-полицай на следующий день сам отвёл девочку к немцам… И её расстреляли одну, там же, на Мыслочанской горе. Еще одна еврейка, муж ее уехал, осталась с маленьким сыном. Спряталась в домике сапожника на нашей улице. А малыш начал плакать, немцы услышали и вывели ее.</p>
            <p>У нее золото с собой было, она его отдала полицаю, Костя его звали, и тот ее отпустил. Она у соседей укрылась, а ночью ее перевезли в партизанскую зону, в отряд Шваякова. Хлеб В Глуске в основном стояли немцы, ну, скажем, третьей категории: штабные немцы в зелёной форме. Среди них попадались и не злые, показывали нам фотографии своих детей. Кухня тут у нас была немецкая через забор, они нам иногда еду давали. Помню, бочечка такая со сгущенкой у них была, они нам остатки отдали, и мама наскребла еще по стенкам много сгущенки.</p>
            <p>А вот когда эсэсовцы пришли, в черных мундирах, с бляхами, было страшно, мы их очень боялись, они были очень жестокие, никого не жалели. Один раз немец из простых дал хлеба мне и Васе Голубу, моему другу. В этот хлеб добавляли опилки, он был твердый и долго хранился, но такой вкусный! Мы ж полуголодные постоянно были. А тут сразу целая буханка! Как мы обрадовались!</p>
            <p>Эсэсовец, а он в обычной одежде был, без формы, подбежал ко мне и начал бить. Хлеб из рук выбил, он упал и по песку покатился (у Василия Ивановича и теперь, спустя столько лет, накатываются на глаза слёзы — авт. ). Сосед мне потом объяснил, что это немец переодетый, и посоветовал, чтоб мы немцев лучше обходили стороной. И с тех пор главное ругательство для любого моего обидчика: немец ты переодетый. На всю жизнь у меня эта обида осталась, я ее никогда не забуду.</p>
            <p>А хлеб мы с другом, конечно, подняли, отерли и съели. Освобождение Когда освобождали Глуск, наши солдаты шли очень быстро. Убегая, немцы оставили на горке возле речки пушку. Она тихоходная была, забрать ее не смогли, поэтому заминировали. Конечно, наши танкисты этого не знали, хотели пушку скатить вниз, и прогремел взрыв. Погибло 6 человек, а в ближних домах ранило осколками местных жителей.</p>
        </div>
    </div>
  )
}

export default ChildMemoryPage