import React from 'react'
import Monument1 from '../images/monuments/image1.jpeg';
import Monument2 from '../images/monuments/image2.jpeg';
import Monument3 from '../images/monuments/image3.jpeg';
import Monument4 from '../images/monuments/image4.jpeg';
import Monument5 from '../images/monuments/image5.jpeg';
import Monument6 from '../images/monuments/image6.jpeg';


const MonumentsPage = () => {
  return (
    <div className='MonumentsPage light'>
        <div className="container">
          <h1 className='bold' style={{textAlign: 'center'}}>Памятники и мемориалы Глуска и Глусского района</h1>
          <div className="monument_info">
            <img src={Monument1}/>
            <div className='monument_text'>
              <h1>Мемориальная доска Дмитрию</h1>
              <p>Один из героев революции и гражданской войны – наш земляк Дмитрий Жижкевич, секретарь первого Глусского волостного Совета. В 1918 году он погиб от рук белополяков. На месте расстрела установлена мемориальная доска, именем Дмитрия Жижкевича названа улица в Глуске. Каждый год в День Октябрьской революции члены КПБ приходят почтить память земляка-революционера.</p>
            </div>

          </div>
          
          <div className="monument_info">
            <img src={Monument2}/>
            <div className='monument_text'>
              <h1>Бульвар Граховского</h1>
              <p>Граховский Сергей Иванович – классик белорусской литературы, поэт и прозаик. Детство и юность провел на Глусчине. В 2003 году к 90-летию со дня рождения в Глуске установлена мемориальная доска, его именем названы бульвар и средняя школа № 1. Почетный гражданин Глуска с 1995 г.</p>
            </div>

          </div>

          <div className="monument_info">
            <img src={Monument3}/>
            <div className='monument_text'>
              <h1>Аллея Славы</h1>
              <p>10 августа 2007г., в центре Глуска в сквере открыта Аллея Славы. Здесь установлено пять памятных знаков с именами наших земляков Героев Советского Союза Степана Калиновского, Ульяна Рыбака, Героев Социалистического Труда Александры Шаплыко, Надежды Рудницкой, Василия Борисика и бюст дважды Героя Советского Союза Степана Шутова. Памятные знаки выполнены в мраморе с изображением портрета Героя и краткой биографической справки. Бюст С.Ф.Шутову был установлен на родине героя ещё в 1949 г. в д. Дворец. В 1975 г. по решению районного комитета партии и с согласия родных бюст перенесли в районный центр и установили в городском сквере. Он и положил начало будущей Аллеи Славы.Аллея Славы – дань уважения и памяти ратному и трудовому подвигу земляков.</p>
            </div>

          </div>

          <div className="monument_info">
            <img src={Monument4}/>
            <div className='monument_text'>
              <h1>Мемориальный комплекс “Воинская слава”</h1>
              <p>В 1974 году в память землякам создан мемориальный комплекс. В центре площадки на насыпном холме – скульптура женщины. На вытянутых руках она держит меч, увенченый лавровой веткой. С трёх сторон к холму ведут лестницы, которые переходят в бетонированные дорожки с цветочными газонами.</p>
            </div>

          </div>

          <div className="monument_info">
            <img src={Monument5}/>
            <div className='monument_text'>
              <h1>Памятник евреям – жертвам фашизма</h1>
              <p>Памятник установлен близ д. Мыслотино на “Мыслочанской горе”. На этом месте 2 декабря 1941 года немецко-фашистские захватчики расстреляли 3 тысячи мирных евреев, проживавших в захваченном оккупантами Глуске, а также советских военнопленных.</p>
            </div>

          </div>

          <div className="monument_info">
            <img src={Monument6}/>
            <div className='monument_text'>
              <h1>Памятный знак воинам – афганцам</h1>
              <div>
                <p>14 февраля 2008 г. накануне Дня памяти воинов-интернационалистов в Глуске состоялось торжественное открытие памятного знака, увековечившего патриотизм и мужество людей, выполнивших свой гражданский и воинский долг на афганской земле.</p>
                <p>Знак выполнен в форме отбитого куска гранита красного цвета, словно олицетворение тех далеких афганских гор. На бронзовой пластине надпись: “1979 – 1989 Афганистан” и стихотворение самодеятельного автора:
“Я не забуду, друг мой боевой,
Как пыль дорог одну глотали,
В походы шли с надеждою одной,
Что смерть промчится где-то стороной,
А рядом мы не раз её видали…”
</p>
                <p>За долгие 10 лет через афганскую войну прошли более 60 наших земляков. Трое из них не вернулись. Тем дороже память о них, тем острее потребность сохранить и передать её потомкам.</p>
              </div>
            </div>

          </div>
        </div>
    </div>
  )
}

export default MonumentsPage